.custom-radio {
  $self: &;
  display: flex;
  flex-wrap: wrap;
  margin-top: -20px;

  @include max(sm) {
    display: block;
    margin-top: -10px;
  }

  &__item {
    display: block;
    line-height: 1;
    width: 50%;
    max-width: 240px;
    cursor: pointer;
    margin-top: 20px;
    padding-right: 20px;

    &:nth-child(even) {
      padding-right: 0;
    }

    @include max(sm) {
      width: 100%;
      margin-top: 10px;
      padding-right: 0;
    }

    & + & {
      //margin-top: 16px;
    }

    @include hover {
      #{$self}__text {
        &::before {
          border-color: $blue;
        }
      }
    }

    input:focus ~ #{$self}__text {
      &::before {
        border-color: $blue;
      }
    }

    input:checked ~ #{$self}__text {
      &::before {
        border-color: $blue;
        background-color: $white;
        box-shadow: inset 0 0 0 3px $blue;
      }
    }
  }

  &__text {
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding-left: 28px;

    font-size: 18px;
    line-height: 1.2;
    color: $darkColor2;
    letter-spacing: 0.1px;

    @include max(sm) {
      font-size: 16px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 3px;
      left: 0;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      border: solid 1px $darkColor2;
      transition: border-color 0.3s ease;
    }
  }
}
