.tabs {
  display: flex;

  & > * {
    align-self: center;
  }

  &__item {
    padding: 0;
    font-size: 18px;
    line-height: 1;
    color: $darkColor2;
    text-align: left;
    border: none;
    background-color: transparent;

    align-self: center;
    transition: color 0.3s ease;
    appearance: none;
    cursor: pointer;

    &--active {
      color: $blue;
      pointer-events: none;
    }

    @include max(sm) {
      font-size: 16px;
    }

    @include hover {
      color: $blue;
    }

    &:active {
      opacity: 0.7;
    }

    & + & {
      margin-left: 65px;

      @include max(sm) {
        margin-left: 25px;
      }
    }
  }
}
