.tariff-grid {
  display: flex;

  margin-left: -20px;

  @include max(md) {
    margin-left: -16px;
  }

  @include max(sm) {
    display: block;
    margin-left: 0;
  }

  &__item {
    width: calc(33.33% - 20px);
    margin-left: 20px;

    @include max(md) {
      width: calc(33.33% - 16px);
      margin-left: 16px;
    }
    @include max(sm) {
      width: 100%;
      margin-left: 0;
      margin-top: 16px;
    }
  }
}
