.search {
  form {
    position: relative;
  }

  &__field {
    input {
      padding-left: 16px;
    }
  }

  &__btn {
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);

    width: 24px;
    height: 24px;
    padding: 0;
    border: none;
    background-color: transparent;
    appearance: none;
    cursor: pointer;

    svg {
      display: block;
      width: 100%;
      height: auto;
      fill: #323232;
      transition: fill 0.3s ease;
    }

    @include hover {
      svg {
        fill: $blue;
      }
    }

    &:active {
      opacity: 0.7;
    }
  }
}
