.guide-section {
  & + & {
    margin-top: 90px;

    @include max(md) {
      margin-top: 45px;
    }

    @include max(sm) {
      margin-top: 40px;
    }
  }

  * {
    font-size: 18px;
    line-height: 1.6;
    letter-spacing: 0.1px;
    font-weight: 300;

    @include max(md) {
      font-size: 16px;
      line-height: 2;
    }

    @include max(sm) {
      font-size: 14px;
    }
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 35px;
    font-size: 22px;
    line-height: 1;
    letter-spacing: 0;
    font-weight: 400;

    @include max(md) {
      margin-bottom: 25px;
      font-size: 20px;
    }

    @include max(sm) {
      //font-size: 14px;
    }
  }

  ol {
    margin: 0;
    padding-left: 10px;
    list-style-position: inside;

    @include max(md) {
      padding-left: 21px;
    }

    @include max(sm) {
      padding-left: 5px;
    }

    li + li {
      margin-top: 15px;

      @include max(md) {
        margin-top: 0;
      }
    }
  }

  ul {
    margin: 38px 30px 30px 94px;

    @include max(md) {
      margin-top: 25px;
      margin-right: 0;
      margin-left: 25px;
    }

    @include max(sm) {
      margin-left: 0;
      padding-left: 22px;
    }

    li {
      line-height: 2;
    }
  }

  li b,
  li strong {
    font-weight: 400;
  }

  a:not(.btn) {
    color: $blue;
    text-decoration: none;
    transition: color 0.3s ease;

    @include hover {
      color: lighten($blue, 10%);
    }

    &:active {
      opacity: 0.7;
    }
  }

  &__video {
    max-width: 768px;
    margin: 90px auto 0;

    @include max(md) {
      margin-top: 45px;
    }

    @include max(sm) {
      margin: 40px 0;
    }

    &-inner {
      position: relative;

      height: 0;
      padding-bottom: 56.25%; /* задаёт высоту контейнера для 16:9 (если 4:3 — поставьте 75%) */
      overflow: hidden;
    }

    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-width: 0;
      outline-width: 0;
    }

    img {
      display: block;
      width: 100%;
      height: auto;
    }
  }

  &__btn {
    margin-top: 90px;

    @include max(md) {
      margin-top: 45px;
    }

    @include max(sm) {
      margin-top: 40px;
    }

    & > * {
      line-height: 1;
      letter-spacing: 0;
    }
  }
}
