.title {
  margin: 0;
  font-size: 50px;
  line-height: 1.2;
  letter-spacing: 0.01em;
  font-weight: 700;
  color: $darkColor;

  @include max(sm) {
    font-size: 32px;
  }

  &--small {
    font-size: 24px;

    @include max(md) {
      font-size: 22px;
    }

    @include max(sm) {
      font-size: 20px;
    }
  }
}
