.accordion {
  $self: &;

  &--aside {
    #{$self}__item {
      border-bottom: none;

      &-control {
        padding: 0 0 0 38px;

        @include max(md) {
          font-size: 22px;
        }

        @include max(sm) {
          font-size: 18px;
        }

        &-icon {
          right: auto;
          left: 0;
          top: 0;
          transform: rotate(-90deg);
        }
      }

      &-content {
        padding: 30px 0;
        background-color: transparent;

        @include max(md) {
          padding-left: 38px;
        }

        ul {
          margin: 0;
          padding: 0;
          list-style: none;
        }

        li {
          line-height: 1;
        }

        li + li {
          margin-top: 30px;
        }

        a {
          font-size: 18px;
          line-height: 1;
          color: $blue;
          text-decoration: none;
          transition: color 0.3s ease;

          @include hover {
            color: lighten($blue, 10%);
          }

          &:active {
            opacity: 0.7;
          }
        }
      }
    }

    #{$self}__item--active {
      #{$self}__item-control-icon {
        transform: rotate(0);
      }
    }

    #{$self}__item + #{$self}__item {
      margin-top: 30px;

      @include max(md) {
        margin-top: 25px;
      }

      @include max(sm) {
        margin-top: 15px;
      }
    }
  }

  &__item {
    $item: &;
    border-bottom: solid 1px rgba($grey-92, 0.5);

    &--active {
      #{$item}-control-icon {
        transform: scale(1, -1);
      }
    }

    &-control {
      $control: &;
      position: relative;
      display: block;
      width: 100%;
      margin: 0;
      padding: 34px 94px 34px 0;
      font-size: 22px;
      line-height: 1;
      text-align: left;
      border: none;
      background-color: transparent;
      cursor: pointer;
      appearance: none;

      transition: color 0.3s ease;

      @include max(md) {
        padding: 24px 48px 24px 0;
        font-size: 18px;
        line-height: 1.2;
      }

      @include max(sm) {
        padding-top: 20px;
        padding-bottom: 20px;
        font-size: 16px;
      }

      @include hover {
        color: $blue;

        #{$control}-icon svg {
          fill: $blue;
        }
      }

      &:active {
        opacity: 0.7;
      }

      &-icon {
        position: absolute;
        top: 34px;
        right: 34px;
        width: 24px;
        height: 24px;
        transition: transform 0.3s ease;

        @include max(md) {
          top: 20px;
          right: 0;
        }

        @include max(sm) {
        }

        svg {
          display: block;
          width: 100%;
          height: auto;
          fill: $darkColor;
          transition: fill 0.3s ease;
        }
      }
    }

    &-content {
      display: none;
      overflow: hidden;
      padding: 60px 100px;
      font-size: 18px;
      line-height: 1.8;
      letter-spacing: 0.1px;
      background-color: rgba($grey-c4, 0.1);

      @include max(md) {
        padding: 46px 38px;
        font-size: 16px;
        line-height: 1.2;
      }

      @include max(sm) {
        padding: 34px 15px;
        font-size: 14px;
      }

      a {
        color: $blue;
        text-decoration: none;
        transition: color 0.3s ease;

        @include hover {
          color: lighten($blue, 10%);
        }

        &:active {
          opacity: 0.7;
        }
      }
    }
  }
}
