.plugin-type {
  display: flex;

  &__icon {
    width: 24px;
    height: 24px;
    margin-right: 14px;
    align-self: center;

    svg {
      display: block;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 1.2;
    color: $darkColor;
    letter-spacing: 0.1px;
    align-self: center;
  }
}
