.socials {
	$self: &;

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	&__item {
		+ #{$self}__item {
			margin-left: 39px;
		}

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 20px;
			height: 20px;
			transition: opacity 0.3s ease;

			@include hover {
				opacity: 0.8;
			}
		}

		svg {
			fill: #00FFFF;
		}
	}
}