.plugin-table {
  font-size: 18px;
  line-height: 1;
  color: $darkColor;
  letter-spacing: 0.1px;

  @include max(sm) {
    font-size: 16px;
  }

  & + & {
    margin-top: 90px;

    @include max(md) {
      margin-top: 40px;
    }
  }

  &__title {
    display: flex;
    margin: 0 0 30px;

    font-size: 22px;
    font-weight: 400;
    letter-spacing: 0;

    @include max(md) {
      padding-bottom: 20px;
      font-size: 20px;
      border-bottom: solid 1px rgba($grey-92, 0.5);
    }

    @include max(sm) {
      //font-size: 18px;
    }

    &-icon {
      width: 24px;
      height: 24px;
      margin-right: 14px;
      flex-shrink: 0;
      align-self: center;

      @include max(md) {
        width: 22px;
        height: 22px;
        margin-right: 10px;
      }

      @include max(sm) {
        width: 20px;
        height: 20px;
      }

      svg {
        display: block;
        width: 100%;
        height: auto;
      }
    }

    &-text {
    }
  }

  &__row {
    display: flex;

    & + & {
      margin-top: 30px;

      @include max(md) {
        margin-top: 20px;
      }
    }
  }

  &__hint {
    font-weight: 300;
  }

  &__text {
    margin-left: 6px;
  }

  a {
    color: $blue;
    text-decoration: none;
    transition: color 0.3s ease;

    @include hover {
      color: lighten($blue, 10%);
    }

    &:active {
      opacity: 0.7;
    }
  }
}
