.plugin-info {
  display: flex;
  width: auto;
  padding: 18px 0;
  border: solid 1px $grey-92;

  @include max(md) {
    padding: 16px 0;
  }

  @include max(sm) {
    display: block;
    padding: 5px 15px;
  }

  &__item {
    display: flex;
    justify-content: center;
    padding: 0 20px;
    flex-grow: 1;

    font-size: 18px;
    line-height: 1;
    color: $darkColor2;

    @include max(md) {
      font-size: 16px;
    }

    @include max(sm) {
      padding: 10px 0;
      justify-content: flex-start;
    }

    & + & {
      border-left: solid 1px rgba($darkColor2, 0.3);

      @include max(sm) {
        border-left: none;
        border-top: solid 1px rgba($darkColor2, 0.3);
      }
    }
  }

  &__hint {
    font-weight: 300;
    align-self: center;
  }

  &__text {
    margin-left: 6px;
    align-self: center;
  }

  &__btn {
    width: 232px;
    margin-top: -19px;
    margin-bottom: -19px;
    margin-right: -1px;

    @include max(md) {
      width: 20%;
      margin-top: -17px;
      margin-bottom: -17px;
    }

    @include max(sm) {
      width: calc(100% + 30px);
      margin-left: -15px;
      margin-top: 0;
      margin-bottom: -6px;
    }

    svg {
      width: 30px;
      height: 30px;

      @include max(md) {
        width: 22px;
        height: 22px;
      }
    }
  }
}
