.header {
  $self: &;

  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  padding: 30px 23px 0;
  box-sizing: border-box;

  @include max(md) {
    flex-direction: column;
    justify-content: center;
    height: 65px;
    padding: 0 20px;
    background: url("../images/header-grad.png") no-repeat center;
    background-size: cover;
  }

  &--inner {
    height: auto;
    padding: 52px 20px 55px;
    background-color: #000000;
    background-repeat: no-repeat;
    background-position: right 0 top 0;
    background-size: auto 100%;
    background-image: url("../images/inner/header-inner-bg.svg");

    justify-content: flex-end;

    @include max(md) {
      padding: 26px 20px 27px;
      background-image: none;
    }

    #{$self}__logo {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 35px;

      @include max(md) {
        left: 20px;
        width: 173px;
      }

      @include min(1920) {
        //left: ;
      }
    }

    #{$self}__canvas {
      width: calc(100% - ((100% - 1030px) / 2));
      margin-left: 300px; //calc((100% - 1030px) / 2);

      @include max(md) {
        width: 100%;
        margin-left: 0;
      }

      ul.header__menu {
        margin-left: 0 !important;
      }
    }

    ul.header__menu {
      justify-content: space-between;
      width: 100%;
      max-width: 1040px;

      li + li {
        margin-left: 20px !important;

        @include max(md) {
          margin-left: 0 !important;
        }
      }
    }
  }

  &__canvas {
    display: flex;
    width: 100%;
    align-items: center;
    overflow: auto;

    @include max(md) {
      position: fixed;
      top: 65px;
      left: 0;

      transform: translateX(100%);
      width: 100%;
      height: calc(100% - 65px);
      padding: 20px;
      z-index: 999;
      background-color: $white;

      display: block;
      pointer-events: none;

      &.is-open {
        transform: translateX(0);
        pointer-events: auto;
      }

      &.is-animate {
        transition: transform 0.3s ease;
      }
    }
  }

  &__logo-left {
    display: none !important;
    position: absolute;
    left: 35px;

    @include max(md) {
      display: flex !important;
    }

    .logo__icon {
      width: 237px;
      height: 58px;
      margin-right: 16px;

      @include max(md) {
        width: 150px;
        height: 36px;
        margin-right: 11px;
        transform: scale(1);
      }
    }
  }

  &__logo-center {
    @include max(md) {
      display: none;
    }
  }

  ul.header__menu {
    display: flex;
    align-items: center;
    margin: 0 auto;

    @include max(md) {
      //display: none;
      margin: 0;
      width: 100%;
      flex-direction: column;
    }

    li {
      + li:not(.js-logo-item) {
        margin-left: 100px;

        @include max(md) {
          margin-left: 0;
        }
      }

      &.js-logo-item {
        margin-left: 50px;

        @include max(md) {
          display: none;
        }

        + li {
          margin-left: 50px;

          @include max(md) {
            margin-left: 0;
          }
        }
      }

      @include max(md) {
        width: 100%;
        margin-left: 0;
        align-self: flex-start;
      }

      a {
        font-size: 15px;
        line-height: 16px;
        color: #ffffff;
        text-transform: uppercase;
        text-decoration: none;
        border-bottom: 1px solid transparent;
        transition: color 0.3s ease, border-bottom-color 0.3s ease;

        @include max(md) {
          display: block;
          padding: 15px 0;
          font-size: 18px;
          line-height: 1.2;
          color: #000000;
          letter-spacing: 0.1px;
          text-transform: none;

          border-bottom: solid 1px rgba($grey-92, 0.5);

          &:active {
            color: $blue;
            border-bottom-color: $blue;
          }
        }

        @include max(sm) {
          font-size: 16px;
        }

        &:not(.header__logo-center) {
          @include hover {
            color: #00ffff;
            border-bottom-color: #12ff72;
          }
        }
      }
    }
  }

  &__social {
    display: none;

    @include max(md) {
      display: flex;
      justify-content: center;
      margin-top: 50px !important;
    }

    li {
      width: 20px;
      margin: 0 20px;

      @include max(sm) {
        margin: 0 10px;
      }
    }

    a {
      display: block;
      height: 20px;

      @include max(md) {
        height: 15px;
      }

      svg {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: scale-down;
      }

      &:active {
        svg {
          fill: $blue;
        }
      }
    }
  }

  &__burger {
    display: none;
    width: 22px;
    height: 12px;
    position: relative;

    @include max(md) {
      display: block;
      align-self: flex-end;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background-color: #00ffff;
      position: absolute;
      left: 0;
    }

    &::before {
      top: 0;
    }

    &::after {
      bottom: 0;
    }

    span {
      width: 100%;
      height: 1px;
      background-color: #00ffff;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
    }

    &.is-open {
      span {
        display: none;
      }

      &::before {
        top: 50%;
        transform: translateY(-50%) rotate(45deg);
      }

      &::after {
        bottom: 50%;
        transform: translateY(50%) rotate(-45deg);
      }
    }
  }

  &__static,
  &__fixed {
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    padding: 30px 23px 0;
    box-sizing: border-box;
  }

  &__static {
    transition: transform 0.4s ease;

    @include max(md) {
      justify-content: flex-end;
    }
  }

  &__fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 70px;
    z-index: 2;
    transform: translateY(-200px);
    transition: opacity 0.4s ease, transform 0.4s ease;

    &::before {
      content: '';
      display: block;
      width: 100%;
      height: 220px;
      position: absolute;
      top: 0;
      left: 0;
      pointer-events: none;
      background: linear-gradient(180deg, #000000 11.35%, rgba(0, 0, 0, 0) 94.78%);
      opacity: 0.8;
    }

    @include max(md) {
      display: none;
    }

    &--visible {
      transform: translateY(0);
    }

    #{$self} {
      &__logo-left {
        display: flex !important;
        opacity: 1;
        pointer-events: auto;
      }
    }

    ul.header__menu {
      position: relative;
      margin: 0 0 0 22.4%;

      @include max(lg) {
        margin-left: 280px;
      }

      li+li:not(.js-logo-item) {
        margin-left: 124px;

        @include max(xl) {
          margin-left: 100px;
        }

        @include max(lg) {
          margin-left: 70px;
        }
      }

      .js-logo-item {
        display: none;
        opacity: 0;
        pointer-events: none;
        transition: opacity 0.5s ease;
      }
    }
  }
}
