.filters {
  &--flex {
    display: flex;

    & > * {
      align-self: center;
    }

    @include max(md) {
      flex-direction: column;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 760px;
    //margin-right: auto;
    //flex-grow: 1;

    @include max(md) {
      width: calc(100% + 20px);
      max-width: none;
      margin-right: 0;
      margin-bottom: -10px;
      padding-bottom: 10px;
      padding-right: 20px;

      overflow: auto;
      align-self: flex-start;
    }

    @include max(sm) {
      width: calc(100% + 15px);
      padding-right: 15px;
    }
  }

  &__search {
    width: 100%;
    max-width: 300px;

    //margin-left: 20px;
    margin-left: 150px;
    flex-shrink: 0;

    @include max(md) {
      max-width: none;
      margin-left: 0;
      margin-bottom: 60px;
      order: -1;
    }

    @include max(sm) {
      margin-bottom: 40px;
    }
  }

  &__item {
    padding: 0;
    font-size: 18px;
    line-height: 1;
    color: $darkColor2;
    text-align: left;
    border: none;
    background-color: transparent;

    align-self: center;
    transition: color 0.3s ease;
    appearance: none;
    cursor: pointer;

    &--active {
      color: $blue;
      pointer-events: none;
    }

    @include hover {
      color: $blue;
    }

    &:active {
      opacity: 0.7;
    }

    & + & {
      margin-left: 20px;

      @include max(md) {
        margin-left: 25px;
      }
    }
  }
}
