.downloads-step {
	$self: &;

	display: flex;
	justify-content: space-between;

	@include max(md) {
		flex-direction: column;
		align-items: center;
	}

	+ #{$self} {
		margin-top: 212px;

		@include max(md) {
			margin-top: 81px;
		}

		@include max(sm) {
			margin-top: 78px;
		}
	}

	&__left {
		max-width: 467px;
		width: 100%;

		@include max(md) {
			max-width: 100%;
			text-align: center;
		}
	}

	&__image {
		position: relative;
		margin-right: -150px;
		margin-top: -60px;

		@include max(md) {
			margin-right: 0;
			margin-top: 0;
		}

		&::before {
			content: '';
			display: block;
			width: 1366px;
			height: 1083px;
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: -1;
			transform: translate(-50%, -50%);
			background: url("../images/bg-8.png") no-repeat center;
			background-size: cover;

			@include max(md) {
				width: 768px;
				height: 609px;
			}
		}

		img {
			position: relative;

			@include max(md) {
				max-width: 545px;
				height: auto;
			}

			@include max(sm) {
				max-width: 320px;
			}
		}

		&--type-2 {
			margin-right: -10px;
			margin-top: -27px;

			@include max(md) {
				margin-right: 0;
				margin-top: 30px;
			}

			&::before {
				width: 1413px;
				height: 1127px;
				background: url("../images/bg-9.png") no-repeat center;
				background-size: cover;

				@include max(md) {
					width: 768px;
					height: 613px;
				}
			}

			img {
				position: relative;

				@include max(md) {
					max-width: 310px;
					height: auto;
				}

				@include max(sm) {
					max-width: 185px;
				}
			}
		}
	}

	&__title {
		display: inline-block;
		font-size: 26px;
		line-height: 100%;
		font-weight: 400;

		@include max(md) {
			font-size: 24px;
		}

		@include max(sm) {
			font-size: 20px;
		}

		span {
			text-decoration-line: underline;
			text-underline-offset: 15px;
		}
	}

	&__text {
		max-width: 830px;
		font-size: 18px;
		line-height: 160%;
		margin-top: 40px;

		@include max(md) {
			max-width: 474px;
			font-size: 16px;
			margin: 29px auto 0;
		}

		@include max(sm) {
			font-size: 14px;
		}

		a {
			color: $blue-light;
			text-decoration: none;
		}
	}

	&__instruction {
		font-size: 24px;
		line-height: 120%;
		font-weight: bold;
		margin-top: 31px;

		@include max(md) {
			max-width: 390px;
			margin: 34px auto 0;
			font-size: 22px;
		}

		@include max(sm) {
			font-size: 18px;
			margin: 41px auto 0;
		}

		br {
			@include max(md) {
				display: none;
			}
		}
	}

	&__buttons {
		display: flex;
		flex-wrap: wrap;
		margin: 43px -15px 0;

		@include max(md) {
			margin: 50px -15px 0;
			justify-content: center;
		}

		@include max(sm) {
			margin: 33px 0 0;
		}

		&--column {
			flex-direction: column;
			align-items: flex-start;

			@include max(md) {
				flex-direction: row;
				align-items: center;
			}
		}
	}

	&__button {
		margin: 0 15px 30px;

		@include max(sm) {
			max-width: 290px;
			width: 100%;
			margin: 0 0 20px;
		}

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 20px 40px;
			background-color: $darkColor;
			transition: background-color 0.3s ease;
			font-size: 18px;
			line-height: 18px;
			color: $white;
			text-decoration: none;

			@include max(md) {
				padding: 11px 25px;
				font-size: 16px;
				line-height: 120%;
			}

			@include max(sm) {
				width: 100%;
				font-size: 14px;
				padding: 8px 20px;
			}

			@include hover {
				background-color: $blue;
			}
		}

		p {
			font-size: 14px;
			line-height: 120%;
			margin-top: 11px;

			@include max(md) {
				font-size: 12px;
				margin-top: 4px;
			}

			@include max(sm) {
				margin-top: 8px;
			}
		}

		svg {
			fill: $white;
			stroke: $white;
			margin-right: 10px;
		}
	}

	&--full {
		#{$self} {
			&__left {
				max-width: 100%;
				width: 100%;
			}
		}
	}
}