.tariff-card {
  $self: &;
  padding: 50px 28px 50px 30px;
  border: solid 1px $grey-92;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;

  @include max(md) {
    padding: 40px 24px;
  }

  @include hover {
    border-color: $blue;
    box-shadow: 0 0 28px rgba(0, 0, 0, 0.2);
  }

  &--recommended {
    border-color: $blue;

    #{$self}__title {
      color: $blue;
    }

    #{$self}__price {
      color: $blue;
    }
  }

  &__title {
    margin: 0 0 20px;
    font-size: 26px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.01em;
  }

  &__description {
    min-width: 66px;
    margin-bottom: 17px;
    font-size: 14px;
    line-height: 1.6;
    letter-spacing: 0.01em;
  }

  &__price {
    margin-bottom: 43px;
    font-size: 41px;
    line-height: 1.2;
    letter-spacing: 0.01em;

    @include max(md) {
      margin-bottom: 36px;
      font-size: 36px;
    }
  }

  &__btn {
    width: 100%;
    margin-bottom: 60px;

    @include max(md) {
      margin-bottom: 40px;
    }
  }

  &__item {
    & + & {
      margin-top: 30px;
    }
  }
}
