.phone-slider {
	position: absolute;
	top: 65%;
	left: 50%;
	z-index: 100;
	transform: translateX(-50%) translateY(-50%);
	opacity: 0;
	width: 360px;
	height: 640px;
	border-radius: 20px;

	@include max(md) {
		width: 224px;
		height: 478px;
	}

	@include max(sm) {
		height: calc(var(--vh, 1vh) * 65);
		width: calc(var(--vh, 1vh) * 36);
	}

	&__outer {
		width: 93%;
		height: 93%;
		overflow: hidden;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		border-radius: 20px;
	}

	&__image {
		height: 100%;

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__wrapper {
		position: absolute;
		top: 0;
		left: 0;
		display: flex;
		width: auto;
	}

	&__slide {
		width: 335px;
		height: 595px;
		border-radius: 20px;
		transition: transform 0.7s linear;

		@include max(md) {
			width: 208px;
			height: 443px;
		}

		@include max(sm) {
			height: calc(var(--vh, 1vh) * 60);
			width: calc(var(--vh, 1vh) * 33.8);
		}

		img {
			display: block;
			width: 100%;
			height: 100%;
			object-position: center;
		}
	}

	&__dots {
		position: absolute;
		left: 50%;
		bottom: calc((-76 * 100vh) / 1080);
		transform: translateX(-50%);
		display: flex;

		@include max(md) {
			bottom: calc((-43 * 100vh)/ 1080);
		}
	}

	&__dot {
		width: 48px;
		height: 1px;
		margin: 0 10px;
		background-color: #fff;

		&--active {
			background-image: linear-gradient(to right, #01ffff 0%, #12ff72 100%);
		}

		@include max(md) {
			width: 36px;
			margin: 0 7px;
		}

		@include max(sm) {
			width: 16px;
			margin: 0 4px;
		}
	}
}
