.custom-file {
  $self: &;
  margin-bottom: 40px;

  &--not-empty {
    #{$self}__output {
      display: block;
    }
  }

  &--complete {
    #{$self}__input {
      opacity: 0.5;
      pointer-events: none;
    }
  }

  &__output {
    display: none;
    margin-bottom: 30px;

    &-title {
      margin-bottom: 14px;

      font-size: 16px;
      line-height: 1;
      color: $darkColor2;
      font-weight: 300;
      letter-spacing: 0.1px;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      margin-left: -12px;
      margin-top: -12px;
    }

    &-item {
      display: flex;
      align-items: center;
      margin-left: 12px;
      margin-top: 12px;
      padding: 9px 12px;
      border: solid 1px $grey-92;
      background-color: $white;

      @include max(sm) {
        padding: 6px 8px;
      }

      &-name {
        margin-right: 12px;
        font-size: 16px;
        line-height: 1;
        color: $darkColor2;
        font-weight: 300;
        letter-spacing: 0.1px;

        @include max(sm) {
          font-size: 14px;
        }
      }

      &-remove {
        display: block;
        width: 24px;
        height: 24px;
        padding: 0;
        border: none;
        background-color: transparent;
        cursor: pointer;

        @include max(sm) {
          width: 20px;
          height: 20px;
        }

        svg {
          display: block;
          width: 100%;
          height: auto;
          fill: $grey;
          transition: fill 0.3s ease;
        }

        @include hover {
          svg {
            fill: $darkColor2;
          }
        }
      }

      input {
        pointer-events: none;
      }
    }
  }

  &__message {
    margin-top: 8px;

    &--error {
      color: red;
    }
  }

  &__wrapper {
    display: flex;

    @include max(sm) {
      flex-direction: column;
    }
  }

  &__btn {
    flex-shrink: 0;

    @include max(sm) {
      width: auto;
      align-self: flex-start;
    }
  }

  &__hint {
    margin-left: 30px;
    font-size: 16px;
    line-height: 1.2;
    color: rgba($darkColor2, 0.5);
    letter-spacing: 0.1px;
    align-self: center;

    @include max(sm) {
      margin-left: 0;
      margin-bottom: 15px;
      order: -1;
      align-self: flex-start;
    }
  }
}
