.form-message {
  color: $darkColor2;
  line-height: 2;
  letter-spacing: 0.1px;

  @include max(md) {
    line-height: 1.2;
  }

  &__title {
    font-size: 22px;

    @include max(md) {
      margin-bottom: 15px;
      font-size: 20px;
    }

    @include max(sm) {
      margin-bottom: 10px;
      font-size: 16px;
    }
  }

  &__text {
    font-size: 18px;
    font-weight: 300;

    @include max(md) {
      font-size: 16px;
    }

    @include max(sm) {
      font-size: 14px;
    }
  }
}
