.infoblock {
  & + & {
    margin-top: 60px;

    @include max(md) {
      margin-top: 40px;
    }
  }

  &__title {
    display: flex;
    margin: 0 0 27px;

    font-size: 22px;
    line-height: 1;
    color: $darkColor;
    font-weight: 400;

    @include max(md) {
      font-size: 20px;
    }

    @include max(sm) {
      font-size: 18px;
    }

    &-icon {
      width: 24px;
      height: 24px;
      margin-right: 14px;
      flex-shrink: 0;
      align-self: center;

      @include max(md) {
        width: 22px;
        height: 22px;
        margin-right: 10px;
      }

      @include max(sm) {
        width: 20px;
        height: 20px;
      }

      svg {
        display: block;
        width: 100%;
        height: auto;
      }
    }

    &-text {
      align-self: center;
    }
  }

  &__content {
    padding-left: 38px;
    font-size: 18px;
    line-height: 1.6;
    letter-spacing: 0.1px;

    @include max(md) {
      padding-left: 32px;
      font-size: 16px;
    }

    @include max(sm) {
      padding-left: 0;
      font-size: 14px;
    }

    p + p {
      margin-top: 30px;
    }

    ul,
    ol {
      padding-left: 18px;

      li + li {
        margin-top: 30px;
      }
    }
  }
}
