.inner-page {
  $self: &;

  padding-bottom: 225px;

  @include max(md) {
    padding-bottom: 60px;
  }

  @include max(sm) {
    padding-bottom: 40px;
  }

  &__breadcrumbs {
    margin-bottom: 105px;

    @include max(md) {
      margin-bottom: 60px;
    }

    @include max(sm) {
      margin-bottom: 40px;
    }
  }

  &__header {
    $header: &;
    margin-bottom: 70px;

    @include max(md) {
      margin-bottom: 30px;
    }

    @include max(sm) {
      margin-bottom: 20px;
    }

    & + .guide-section {
      margin-top: 93px;

      @include max(md) {
        margin-top: 63px;
      }

      @include max(sm) {
        margin-top: 40px;
      }
    }

    &--flex {
      display: flex;
      justify-content: space-between;

      @include max(md) {
        display: block;
      }

      & > * {
        align-self: center;
      }
    }

    &-plagin-type {
      margin-left: 20px;

      @include max(md) {
        margin-left: 0;
        margin-top: 30px;
      }

      @include max(sm) {
        margin-top: 20px;
      }
    }

    &-search {
      width: 300px;
      margin-left: 24px;

      @include max(md) {
        width: 100%;
        margin-left: 0;
        margin-top: 30px;
      }

      @include max(sm) {
        margin-top: 20px;
      }
    }
  }

  &__intro {
    padding-bottom: 70px;

    font-size: 22px;
    line-height: 1.6;

    @include max(md) {
      padding-bottom: 45px;
      font-size: 20px;
    }

    @include max(sm) {
      padding-bottom: 25px;
      font-size: 16px;
    }

    .register-block {
      margin-top: 46px;
    }

    & + .accordion {
      margin-top: -30px;

      @include max(md) {
        margin-top: -5px;
      }
    }
  }

  &__filters {
    margin-bottom: 90px;

    @include max(sm) {
      margin-bottom: 40px;
    }
  }

  &__plugin-info {
    margin-bottom: 90px;

    @include max(md) {
      margin-bottom: 60px;
    }

    @include max(sm) {
      margin-bottom: 40px;
    }
  }

  &__tabs {
    margin-bottom: 64px;

    @include max(md) {
      margin-bottom: 40px;
    }
  }

  &__grid {
    display: flex;

    @include max(md) {
      display: block;
    }

    #{$self}__main {
      width: calc(100% - 370px);
      margin-right: auto;

      @include max(md) {
        width: 100%;
        margin-right: 0;
      }
    }
  }

  &__aside {
    width: 100%;
    max-width: 300px;
    margin-bottom: 22px;
    padding-left: 30px;
    border-left: solid 1px $grey-92;

    @include max(md) {
      max-width: none;
      margin-bottom: 0;
      margin-top: 60px;
      padding-top: 60px;
      padding-left: 0;
      border-left: none;
      border-top: solid 1px rgba($grey-92, 0.5);
    }

    @include max(sm) {
      margin-top: 40px;
      padding-top: 40px;
    }

    &--no-offsets {
      margin-bottom: 0;

      @include max(md) {
        margin-top: 60px;
        padding-top: 0;
        border-top: none;
      }

      @include max(sm) {
        margin-top: 40px;
        padding-top: 0;
      }
    }

    .accordion {
      width: calc(100% + 8px);
    }
  }
}
