.btn {
  display: inline-flex;
  justify-content: center;
  min-width: 160px;
  padding: 19px 30px;

  font-size: 18px;
  line-height: 1;
  letter-spacing: 0;
  color: $white;
  text-decoration: none;

  border: none;
  background-color: $darkColor;

  transition: background-color 0.3s ease;
  appearance: none;
  cursor: pointer;

  @include max(md) {
    padding-top: 13px;
    padding-bottom: 13px;
    font-size: 16px;
  }

  @include max(sm) {
    display: flex;
    width: 100%;
    padding-top: 12px;
    padding-bottom: 12px;
    font-size: 14px;
  }

  &--large {
    min-width: 330px;
    padding-top: 26px;
    padding-bottom: 26px;

    @include max(md) {
      min-width: 0;
      padding-top: 13px;
      padding-bottom: 13px;
    }

    @include max(sm) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  &--small {
    padding-top: 13px;
    padding-bottom: 14px;

    @include max(md) {
      padding-top: 13px;
      padding-bottom: 13px;
    }

    @include max(sm) {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  &--invert {
    background-color: $blue;

    @include hover {
      background-color: $darkColor !important;
    }
  }

  &[disabled],
  &--disabled {
    background-color: rgba($darkColor, 0.2);
    pointer-events: none;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-self: center;
    width: 30px;
    height: 30px;
    margin: -6px 12px -6px 0;

    @include max(md) {
      margin-top: -7px;
      margin-bottom: -7px;
    }

    @include max(sm) {
      margin-top: -8px;
      margin-bottom: -8px;
    }

    svg {
      display: block;
      align-self: center;
      fill: $white;
    }
  }

  &__text {
    align-self: center;
  }

  @include hover {
    background-color: $blue;
  }

  &:active {
    opacity: 0.7;
  }
}
