.plugins-grid {
  display: flex;
  flex-wrap: wrap;
  margin-top: -60px;
  margin-left: -60px;

  @include max(md) {
    margin-left: -20px;
    margin-top: -32px;
  }

  @include max(sm) {
    margin-left: 0;
    margin-top: -20px;
  }

  &__item {
    width: calc(50% - 60px);
    margin-top: 60px;
    margin-left: 60px;

    @include max(md) {
      width: calc(50% - 20px);
      margin-left: 20px;
      margin-top: 32px;
    }

    @include max(sm) {
      width: 100%;
      margin-left: 0;
      margin-top: 20px;
    }
  }

  &__bottom {
    width: 100%;
    margin-top: 90px;

    @include max(md) {
      margin-top: 60px;
    }

    @include max(sm) {
      margin-top: 40px;
    }
  }

  &__btn {
    display: flex;
    width: 100%;
    max-width: 330px;
    margin: 0 auto;

    @include max(md) {
      max-width: 225px;
    }

    @include max(sm) {
      max-width: none;
    }
  }
}
