.plugin-card {
  display: flex;
  flex-direction: column;
  padding: 30px 30px 0;

  border: solid 1px #929292;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;

  @include max(md) {
    padding: 20px 20px 0;
  }

  @include max(sm) {
    padding: 15px 15px 0;
  }

  @include hover {
    border-color: $blue;
    box-shadow: 0px 0px 28px rgba(0, 0, 0, 0.2);
  }

  &__header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 26px;

    @include max(md) {
      margin-bottom: 20px;
    }

    @include max(sm) {
      margin-bottom: 15px;
    }
  }

  &__main {
    flex-grow: 1;
    padding-bottom: 30px;

    @include max(md) {
      padding-bottom: 20px;
    }

    @include max(sm) {
      padding-bottom: 15px;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    width: calc(100% + 60px);
    margin-left: -30px;
    padding: 15px 30px;
    background-color: rgba(#c4c4c4, 0.1);

    @include max(md) {
      width: calc(100% + 40px);
      margin-left: -20px;
      padding: 15px 20px;
    }

    @include max(sm) {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 15px;
    }
  }

  &__type {
    display: flex;

    &-icon {
      width: 24px;
      height: 24px;
      margin-right: 14px;
      align-self: center;

      svg {
        display: block;
      }
    }

    &-text {
      font-size: 16px;
      line-height: 1.2;
      color: $darkColor;
      letter-spacing: 0.1px;
      align-self: center;
    }
  }

  &__download {
    display: block;
    width: 24px;
    height: 24px;

    svg {
      display: block;
      fill: $darkColor;
      transition: fill 0.3s ease;
    }

    @include hover {
      svg {
        fill: $blue;
      }
    }
  }

  &__title {
    margin: 0 0 10px;

    a {
      color: $darkColor;
      text-decoration: none;
      transition: color 0.3s ease;

      @include hover {
        color: $blue;
      }

      &:active {
        opacity: 0.7;
      }
    }
  }

  &__text {
    font-size: 16px;
    line-height: 1.6;
    color: $darkColor2;
    font-weight: 300;
    letter-spacing: 0.1px;

    @include max(md) {
      font-size: 14px;
    }
  }

  &__info {
    //width: 33.33%;
    padding-right: 20px;

    font-size: 13px;
    line-height: 1.2;
    color: $darkColor2;

    @include max(md) {
      font-size: 12px;
    }

    &-hint {
      opacity: 0.5;
    }

    &:last-child {
      padding-right: 0;
    }
  }
}
