.field {
  $self: &;

  display: block;

  &--req {
    #{$self}__hint {
      &::after {
        content: "*";
        display: inline-block;
        vertical-align: top;
        margin-left: 4px;
        color: red;
      }
    }
  }

  input,
  textarea {
    display: block;
    width: 100%;
    padding: 10px 40px 9px 30px;
    font-size: 16px;
    line-height: 1;

    color: $darkColor2;
    letter-spacing: 0.1px;

    border: solid 1px $grey-92;

    transition: border-color 0.3s ease;
    appearance: none;

    &::placeholder {
      color: rgba($darkColor2, 0.5);
      transition: color 0.3s ease;
      opacity: 1;
    }

    &:focus + #{$self}__clear {
      display: block;
    }

    @include min(md) {
      &:hover {
        border-color: rgba($blue, 0.5);

        &::placeholder {
          color: rgba($darkColor2, 0.75);
        }
      }
    }

    &:focus {
      outline: none;
      border-color: $blue;
    }
  }

  textarea {
    height: 80px;
    resize: none;

    @include max(sm) {
      height: 110px;
    }
  }

  &__wrapper {
    display: block;
    position: relative;
  }

  &__hint {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 12px;
    font-size: 16px;
    line-height: 1;

    color: $darkColor2;
    letter-spacing: 0.1px;
  }

  &__clear {
    display: none;

    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: none;

    @include hover {
      display: block;

      svg {
        fill: $blue;
      }
    }

    &:active {
      opacity: 0.7;
    }

    svg {
      display: block;
      fill: $grey;
      transition: fill;
    }
  }
}
