.breadcrumbs {
  padding: 16px 0;

  background-color: #f3f3f3;

  @include max(md) {
    padding: 11px 0;
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    & > * {
      display: inline-flex;
      font-size: 13px;
      line-height: 1;
      color: #868e86; //$gray-a4;

      @include max(md) {
        font-size: 12px;
      }

      &::after {
        content: "";
        width: 14px;
        height: 14px;
        margin-left: 6px;
        align-self: center;

        background-repeat: no-repeat;
        background-position: 50% 50%;

        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3.43 2.404 8.027 7 3.43 11.597l1.237 1.237L10.5 7 4.667 1.167 3.43 2.404Z' fill='%23868E86'/%3e%3c/svg%3e");
      }
    }

    & > * + * {
      position: relative;
      margin-left: 6px;

      /*&::before {
        content: ">";
        position: absolute;
        left: -15px;
        pointer-events: none;
      }*/
    }

    & > *:last-child {
      &::after {
        display: none;
      }
    }
  }

  a {
    text-decoration: none;
    transition: color 0.3s ease;

    @include hover {
      color: $darkColor;

      &::before {
        //color: $gray-a4;
      }
    }
  }

  span {
    color: #000000;
  }
}
