root {
  --vh: 1vh;
  --vw: 1vw;
  --coof: 1;
}

html {
  //background: #000000;
}
body {
  font-family: $mainFont;
  color: $textMainColor;
  background: #000000;

  &.scroll-hide {
    overflow: hidden;
  }

  &.inner {
    display: flex;
    flex-direction: column;
    width: 100%;
    color: $darkColor;
    background-color: #ffffff;

    main {
      flex-grow: 1;
    }

    & > .header,
    & > .footer {
      position: relative;
    }
  }
}

main {
  position: relative;
}

body::-webkit-scrollbar {
  width: 5px;
  background: #000000;
}

body::-webkit-scrollbar-thumb {
  background: #00ffff;
}

.land {
  overflow: hidden;
}

.top-text {
  font-style: normal;
  font-weight: normal;
  font-size: 35px;
  font-size: #{"max(35px, 1.82vw)"};
  line-height: 130%;
  text-align: center;
  color: #ffffff;
  /*@include max(1100){
		line-height: 120%;
		font-size: 22px;
		font-size: #{"max(22px, 2.86vw)"};
	}*/

  @include max(md) {
    font-size: 22px;
    line-height: 120%;
  }

  @include max(sm) {
    font-size: 18px;
  }

  /*@include max(600){
		font-size: 18px;
		font-size: #{"max(18px, 5.625vw)"};
	}*/
}

.bottom-text {
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  font-size: #{"max(25px, 1.3vw)"};
  line-height: 130%;
  text-align: center;
  color: #ffffff;
  @include max(1100) {
    line-height: 120%;
    font-size: 20px;
    font-size: #{"max(20px, 2.625vw)"};
  }
  @include max(600) {
    font-size: 18px;
    font-size: #{"max(18px, 5.625vw)"};
  }
}

.gradient-text {
  font-style: normal;
  font-weight: 900;
  line-height: 100%;
  text-align: center;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  font-size: 73px;
  font-size: #{"max(73px, 3.8vw)"};
  position: relative;
  white-space: pre-line;

  @include max(1100) {
    line-height: 120%;
    font-size: 40px;
    font-size: #{"max(40px, 5.2vw)"};
  }
  @include max(600) {
    font-size: 26px;
    font-size: #{"max(26px, 8.125vw)"};
  }

  span {
    &:first-child {
      color: #00ffff;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    &:last-child {
      display: block;
      background: linear-gradient(90deg, #00ffff 0, #12ff72);
      -webkit-background-clip: text;
      background-clip: text;
      -webkit-text-fill-color: transparent;
      animation: fade-loop 2s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0s infinite,
      contrast 2s 0.75s forwards;
    }
  }
}

.scroll {
  height: 100vh !important;
  height: calc(var(--vh, 1vh) * 100);
  position: relative;
}

.first-block,
.second-block,
.third-block,
.fourth-block,
.fifth-block,
.sixth-block,
.seventh-block,
.eighth-block {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  box-sizing: border-box;
  //pointer-events: none;
}

.first-block {
  padding-bottom: calc(var(--vh, 1vh) * var(--coof, 1) * 23);
  width: 90%;
  margin: 0 auto;

  &__top {
    opacity: 0;
    position: relative;
  }
  &__center {
    margin: 44px 0 35px;
    opacity: 0;
    position: relative;
    @include max(1100) {
      margin: 10px 0;
    }
    @include max(600) {
      margin: 15px 0;
    }
  }
  &__bottom {
    opacity: 0;
    position: relative;
    max-width: 90%;
  }
}

.hidden-block {
  height: 150vh;

  &--200vh {
    height: 200vh;
  }
}

.second-block {
  width: 100%;

  &__slider {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    @include max(md) {
      margin-top: 65px !important;
    }
  }

  &__bg {
    position: absolute;
    top: 50%;
    right: 0;
    z-index: -1;
    opacity: 0;
    overflow: hidden;
    transform: translateY(-50%);

    @include max(md) {
      padding-left: 10%;
    }

    @include max(sm) {
      height: 100%;
      padding-left: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: left center;

      @include max(md) {
        object-position: left center;
      }

      @include max(sm) {
        object-position: 75% center;
      }
    }
  }

  &__bg-2 {
    display: block;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background: url("../images/bg-7.png") no-repeat center;
    background-size: cover;
    opacity: 0;
  }

  &__top,
  &__center,
  &__bottom {
    opacity: 0;
  }

  &__top {
    @include max(md) {
      font-size: 22px;
      line-height: 120%;
      letter-spacing: 0.01em;
    }

    @include max(sm) {
      font-size: 18px;
    }

    br {
      display: none;

      @include max(sm) {
        display: block;
      }
    }
  }

  &__center {
    margin-top: 40px;

    @include max(md) {
      margin-top: 12px;
    }

    b {
      font-size: 31px;
      font-weight: 400;
      position: relative;
      top: -30px;
      left: -8px;

      @include max(md) {
        font-size: 24px;
        top: -12px;
        left: 4px;
      }

      @include max(sm) {
        top: -20px;
        left: -3px;
      }

      @include max(xs) {
        top: -10px;
      }
    }
  }

  &__bottom {
    font-size: 16px;
    line-height: 160%;
    color: rgba(#e7e7e7, 0.6);
    margin-top: 40px;

    @include max(md) {
      font-size: 14px;
      margin-top: 12px;
    }

    @include max(sm) {
      font-size: 12px;
    }

    br {
      display: none;

      @include max(sm) {
        display: block;
      }
    }
  }
}

.third-block {
  &__bg {
    width: 100%;
    height: calc(var(--vh, 1vh) * 162.33);
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0;
    background: url("../images/bg-4.png") center center no-repeat;
    background-size: cover;
    pointer-events: none;
    opacity: 0;
  }

  &__top,
  &__center {
    max-width: 885px;
    position: relative;
  }

  &__top {
    font-size: 26px;
    line-height: 130%;

    @include max(md) {
      font-size: 22px;
      line-height: 120%;
    }

    @include max(sm) {
      font-size: 18px;
    }

    br {
      display: none;

      @include max(sm) {
        display: block;
      }
    }
  }

  &__center {
    font-size: 50px;
    line-height: 100%;
    margin-top: 40px;

    @include max(md) {
      font-size: 40px;
      line-height: 48px;
      margin-top: 10px;
    }

    @include max(sm) {
      font-size: 26px;
      line-height: 31px;
      margin-top: 15px;
    }
  }

  &__gadgets {
    display: flex;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    img {
      position: relative;
      opacity: 0;

      @include max(md) {
        object-fit: contain;
      }

      &.laptop {
        top: 10px;
        left: -26%;

        @include max(md) {
          width: 77%;
          transform: translateX(-50%);
        }

        @include max(sm) {
          width: 90%;
          top: 27px;
        }
      }

      &.phone {
        top: 125px;
        right: 0;

        @include max(md) {
          width: 19%;
          top: 112px;
        }

        @include max(sm) {
          width: 22%;
        }
      }
    }
  }
}

.fourth-block {
  justify-content: flex-start;
  position: relative;

  &__top {
    max-width: 741px;
    margin: 42px auto 0;
    position: relative;
    opacity: 0;
    font-size: 26px;
    line-height: 130%;


    @include max(md) {
      max-width: 550px;
      margin: 13px auto 0;
      font-size: 22px;
      line-height: 120%;
    }

    @include max(sm) {
      max-width: 270px;
      font-size: 18px;
    }
  }

  &__center {
    font-size: 50px;
    position: relative;
    opacity: 0;

    @include max(md) {
      font-size: 40px;
      line-height: 48px;
    }

    @include max(sm) {
      font-size: 26px;
      line-height: 31px;
    }
  }

  &__phone-wrapper {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    @include max(md) {
      margin-top: 65px !important;
    }
  }

  &__phone {
    position: absolute;
    transform: translateY(-50%);

    @include max(md) {
      margin-top: 0;
    }

    img {
      height: calc(var(--vh, 1vh) * 75.67);
      width: calc(var(--vh, 1vh) * 36);
    }
  }

  &__video {
    display: block;
    width: 100%;
    height: 100%;
    opacity: 0;
    position: relative;

    &::before,
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 33.3333%;
      position: absolute;
      left: 0;
    }

    &::before {
      top: 0;
      background: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 94.78%);
    }

    &::after {
      bottom: 0;
      background: linear-gradient(0, #000000 0%, rgba(0, 0, 0, 0) 94.78%);
    }

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      opacity: 0.2;
    }
  }
}

.fifth-block {
  justify-content: flex-start;

  &__content {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding-top: 124px;
    position: relative;
    overflow: hidden;

    &::before {
      content: "";
      display: block;
      width: 100%;
      height: 20%;
      background: linear-gradient(0, #000000 0%, rgba(0, 0, 0, 0) 94.78%);
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 3;
    }
  }

  &__top,
  &__center {
    position: relative;
    z-index: 1;
  }

  &__top {
    max-width: 600px;
    margin: 42px auto 100px;
    font-size: 26px;
    line-height: 130%;
    opacity: 0;

    @include max(md) {
      max-width: 500px;
      margin: 13px auto 100px;
      font-size: 22px;
      line-height: 120%;
    }

    @include max(sm) {
      margin: 12px auto 40px;
      font-size: 18px;
    }

    br {
      display: none;

      @include max(sm) {
        display: block;
      }
    }
  }

  &__center {
    font-size: 50px;
    opacity: 0;

    @include max(md) {
      font-size: 40px;
    }

    @include max(sm) {
      font-size: 26px;
      line-height: 120%;
    }
  }

  &__marquee {
    width: 100%;
    overflow: hidden;
    position: absolute;
    top: 61%;
    transform: translateY(-50%);
    left: 0;
    opacity: 0;

    @include max(md) {
      top: 61%;
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background: linear-gradient(
          180deg,
          rgba(0, 0, 0, 0.8) 0%,
          rgba(0, 0, 0, 0) 94.78%
      );
    }

    .js-marquee {
      height: 537px;

      @include max(md) {
        height: 430px;
      }

      @include max(sm) {
        height: 253px;
      }
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__hand {
    position: relative;
    margin-top: auto;
    left: 0;
    bottom: -100px;
    z-index: 2;
    opacity: 0;

    img {
      height: 75vh;
      position: relative;
      left: 54%;
      bottom: 0;
      transform: translateX(-50%);

      @include max(md) {
        height: 56vh;
      }

      @include max(sm) {
        height: 52vh;
      }
    }
  }

  &__offset {
    height: 250vh;

    @include max(md) {
      height: 210vh;
    }
  }
}

.sixth-block {
  justify-content: flex-start;

  &__bg {
    width: 100%;
    height: 100%;
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0;
    background: url("../images/bg-5.png") center bottom no-repeat;
    background-size: 70%;
    pointer-events: none;
    opacity: 0;

    @include max(md) {
      background-size: 120%;
    }

    @include max(sm) {
      background-position: center 70%;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    width: 100%;
    position: relative;
    top: 18%;
    transform: translateY(-18%);
  }

  &__center {
    max-width: 871px;
    font-size: 50px;

    @include max(md) {
      font-size: 40px;
      margin: 0 130px;
    }

    @include max(sm) {
      max-width: 280px;
      font-size: 26px;
      line-height: 120%;
      margin: 0 20px;
    }
  }
}

.seventh-block {
  height: auto;
  overflow: hidden;
  padding: 100px 113px 78px;

  @include max(lg) {
    padding: 78px 0;
  }

  &__wrapper {
    width: 100%;
    position: relative;
  }

  &__gradient {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #00ffff;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 1;
      background: linear-gradient(137.02deg, #00ffff 30.14%, #12ff72 112.19%);
      animation: fade-loop 2s cubic-bezier(0.68, -0.55, 0.27, 1.55) 0s infinite,
      contrast 2s 0.75s forwards;
    }
  }

  &__content {
    padding: 185px 214px 192px;
    position: relative;
    z-index: 1;

    @include max(lg) {
      padding: 149px 60px 108px;
    }

    @include max(sm) {
      padding: 90px 23px 100px;
    }
  }

  &__title {
    max-width: 630px;
    font-size: 60px;
    line-height: 120%;
    font-weight: 900;
    color: #000;
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto 43px;

    @include max(lg) {
      font-size: 40px;
      margin: 0 auto 12px;
    }

    @include max(sm) {
      font-size: 26px;
      line-height: 31px;
    }

    br {
      display: none;

      @include max(sm) {
        display: block;
      }
    }
  }

  &__subtitle {
    font-size: 26px;
    line-height: 130%;
    color: #000;
    text-align: center;
    margin-bottom: 135px;

    @include max(lg) {
      height: 120%;
    }

    @include max(md) {
      font-size: 22px;
      line-height: 120%;
      margin-bottom: 70px;
    }

    @include max(sm) {
      font-size: 18px;
    }
  }

  &__row {
    display: flex;
    align-items: center;
    position: relative;

    @include max(lg) {
      flex-direction: column;
    }

    + .seventh-block__row {
      margin-top: 180px;

      @include max(lg) {
        margin-top: 83px;
      }

      @include max(sm) {
        margin-top: 68px;
      }
    }

    .seventh-block__item {
      &:first-child {
        width: 41%;
        margin-right: 10%;

        @include max(lg) {
          width: 100%;
          margin-right: 0;
        }
      }

      &:last-child {
        width: 36%;

        @include max(lg) {
          width: 100%;
          margin-top: 33px;
        }

        @include max(sm) {
          margin-top: 22px;
        }
      }
    }

    &--reverse {
      flex-direction: row-reverse;

      @include max(lg) {
        flex-direction: column;
      }

      .seventh-block__item {
        &:first-child {
          width: 28%;
          margin-right: auto;

          @include max(lg) {
            width: 100%;
            margin-right: 0;
          }
        }

        &:last-child {
          width: 39%;
          margin-right: 12%;

          @include max(lg) {
            width: 100%;
            margin-right: 0;
          }
        }

        p {
          text-align: right;

          @include max(lg) {
            text-align: center;
          }
        }
      }
    }
  }

  &__item {
    position: relative;

    p {
      color: #000;

      @include max(lg) {
        text-align: center;
        max-width: 440px;
        margin: 0 auto;
      }

      &:first-child {
        font-size: 30px;
        line-height: 120%;
        font-weight: 700;

        @include max(lg) {
          font-size: 22px;
          line-height: 26px;
        }

        @include max(sm) {
          font-size: 18px;
          line-height: 22px;
        }
      }

      &:last-child {
        font-size: 22px;
        line-height: 130%;
        margin-top: 21px;

        @include max(lg) {
          font-size: 16px;
          line-height: 160%;
          margin-top: 13px;
        }

        @include max(sm) {
          font-size: 14px;
        }
      }
    }
  }

  &__image {
    img {
      margin-left: auto;

      @include max(lg) {
        width: 35%;
        height: auto;
        margin: 0 auto;
      }

      @include max(sm) {
        width: 47%;
      }
    }
  }
}

.eighth-block {
  position: relative;
  padding-bottom: 212px !important;

  @include max(md) {
    padding-top: 65px !important;
    padding-bottom: 197px !important;
  }

  @include max(sm) {
    padding-bottom: 182px !important;
  }

  &__bg {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    background: url("../images/bg-6.png") center center no-repeat;
    background-size: cover;
    pointer-events: none;
    opacity: 0;
  }

  &__center {
    max-width: 900px;
    font-size: 50px;
    line-height: 50px;
    opacity: 0;

    @include max(md) {
      font-size: 40px;
      line-height: 120%;
      margin: 0 50px;
    }

    @include max(sm) {
      max-width: 280px;
      font-size: 26px;
      margin: 0 20px;
    }
  }

  &__btn {
    padding: 32px 68px;
    margin-top: 85px;
    background-color: #021522;
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 0.08em;
    text-decoration: none;
    transition: background-color 0.3s ease;
    opacity: 0;

    @include hover {
      background-color: rgba(#021522, 0.8);
    }

    @include max(md) {
      font-size: 16px;
      line-height: 100%;
      letter-spacing: normal;
      padding: 25px 46px;
      margin-top: 70px;
    }

    @include max(sm) {
      font-size: 14px;
      margin-top: 63px;
      padding: 12px 24px;
    }
  }

  &__offset {
    height: 285vh;

    @include max(md) {
      height: 300vh;
    }

    @include max(sm) {
      height: 255vh;
    }
  }
}

.phone-animation {
  &__bg {
    width: 100%;
    height: calc(var(--vh, 1vh) * 74.77);
    z-index: -1;
    position: absolute;
    left: 0;
    bottom: 0;
    background: url("../images/bg1.png") center center no-repeat;
    background-size: auto 100%;
    pointer-events: none;
    opacity: 0;
    @include max(1100) {
      background-size: 120% 100%;
    }
  }
  &__icons {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    pointer-events: none;
  }
  &__icon {
    display: flex;
    align-items: flex-start;
    position: absolute;

    height: calc(var(--vh, 1vh) * var(--coof, 1) * 23);
    width: calc(var(--vh, 1vh) * var(--coof, 1) * 23);
    left: 50%;
    bottom: calc(var(--vh, 1vh) * var(--coof, 1) * -23);
    transform: translate(-50%);
    z-index: 10;
    opacity: 0;

    &-inner {
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      position: relative;
      display: flex;
      background: #222222;
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.8);
      transition: box-shadow 0.5s;
    }

    img {
      display: block;
      max-width: 90%;
    }

    &:nth-child(1) {
      z-index: 10;
      order: 4;
      img {
        width: 46.116%;
      }
    }
    &:nth-child(2) {
      z-index: 9;
      order: 3;
      justify-content: flex-end;
      .phone-animation__icon-inner {
        width: 83.6%;
        height: 83.6%;
        right: 92.6%;
        top: 16.4%;
      }
      img {
        width: 40.461%;
      }
    }
    &:nth-child(3) {
      z-index: 9;
      order: 5;
      justify-content: flex-start;
      .phone-animation__icon-inner {
        width: 83.6%;
        height: 83.6%;
        left: 92.6%;
        top: 16.4%;
      }
      img {
        width: 40.461%;
      }
    }
    &:nth-child(4) {
