.phone-block {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 100%;
  left: 0;
  pointer-events: none;
  z-index: 3;

  &__bg {
    z-index: -1;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    height: calc(var(--vh, 1vh) * 162.33);
    background: url("../images/bg2.png") center center no-repeat;
    background-size: auto 100%;
    opacity: 0;
    @include max(1100) {
      height: calc(var(--vh, 1vh) * 120);
    }
  }
  &__inner {
    height: calc(var(--vh, 1vh) * 75.67);
    width: calc(var(--vh, 1vh) * 36);

    @include max(md) {
      margin-top: 65px;
    }

    img {
      width: 100%;
      height: 100%;
      display: block;
      object-fit: contain;
    }
  }
}

/* Удалить круглые углы */
input[type="search"] {
  -webkit-appearance: none;
}

/* Скрыть кнопку отмены */
::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

/* Скрыть лупу */
::-webkit-search-results-button {
  -webkit-appearance: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
