.logo {
	display: flex;
	align-items: center;
	text-decoration: none;

	&__icon {
		width: 105px;
		height: 76px;
		margin-right: 2px;
		z-index: 1;

		@include max(md) {
			width: 50px;
			height: 36px;
			margin-right: 11px;
			transform: scale(1);
		}

		svg {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: 20px;
		line-height: 24px;
		font-weight: 700;
		color: #00ffff;
		letter-spacing: 0.12em;

		@include max(md) {
			font-size: 17px;
			line-height: 17px;
		}

		span {
			font-size: 19px;
			color: #12ff72;

			@include max(md) {
				font-size: 16px;
				line-height: 16px;
			}
		}
	}
}