.downloads {
	overflow: hidden;

	&__breadcrumbs {
		margin-top: 128px;

		@include max(md) {
			margin-top: 65px;
		}
	}

	&__container {
		padding-top: 113px;

		@include max(md) {
			padding-top: 55px;
		}

		@include max(sm) {
			padding-top: 69px;
		}
	}

	&__title {
		font-size: 50px;
		line-height: 100%;
		margin-bottom: 176px;

		@include max(md) {
			font-size: 40px;
			line-height: 120%;
			margin-bottom: 94px;
		}

		@include max(sm) {
			font-size: 28px;
			margin-bottom: 81px;
		}
	}

	&__list {
		margin-top: 210px;

		@include max(md) {
			margin-top: 93px;
		}

		@include max(sm) {
			margin-top: 105px;
		}
	}

	&__list-title {
		font-size: 24px;
		line-height: 120%;
		font-weight: bold;

		@include max(md) {
			font-size: 22px;
			text-align: center;
		}

		@include max(sm) {
			font-size: 18px;
		}
	}

	ol {
		max-width: 980px;
		margin-top: 26px;
		padding-left: 25px;

		@include max(md) {
			padding: 0 50px 0 64px;
		}

		@include max(sm) {
			padding: 0 0 0 23px;
		}

		li {
			font-size: 18px;
			line-height: 160%;

			@include max(sm) {
				font-size: 16px;
			}

			+ li {
				margin-top: 34px;

				@include max(md) {
					margin-top: 24px;
				}
			}

			p {
				font-weight: bold;
			}

			span {
				font-weight: 300;

				@include max(md) {
					font-size: 16px;
				}

				@include max(sm) {
					font-size: 14px;
				}
			}
		}
	}
}