.footer {
	width: 100%;
	background-color: #000;
	padding: 61px 114px 32px;
	position: relative;
	bottom: 0;
	left: 0;
	z-index: 100;

	@include max(1399) {
		padding: 61px 50px 32px;
	}

	@include max(md) {
		padding: 27px 19px 32px;
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include max(md) {
			align-items: flex-start;
		}
	}

	&__bottom {
		display: none;

		@include max(lg) {
			display: flex;
			justify-content: space-between;
			margin-top: 25px;
		}

		@include max(sm) {
			margin-top: 22px;
		}

		a {
			font-size: 14px;
			line-height: 16px;
			color: #545454;
		}
	}

	&__left {
		display: flex;
		flex-grow: 1;
	}

	&__right {
		align-self: flex-start;

		@include max(md) {
			padding-top: 7px;
		}

		@include max(sm) {
			display: none;
		}
	}

	&__info {
		margin-right: 194px;

		@include max(xl) {
			margin-right: 80px;
		}

		@include max(md) {
			margin-right: 20px;
		}

		@include max(sm) {
			margin-right: 0;
		}
	}

	&__logo {
		.logo__icon {
			width: 237px;
			height: 58px;

			@include max(md) {
				width: 150px;
				height: 36px;
			}
		}
	}

	&__copyrights {
		font-size: 14px;
		line-height: 130%;
		color: #545454;
		margin-top: 25px;

		@include max(md) {
			max-width: 390px;
		}

		@include max(sm) {
			max-width: 100%;
			font-size: 12px;
			line-height: 130%;
			margin-top: 19px;
		}

		br {
			@include max(md) {
				display: none;
			}
		}
	}

	&__nav {
		padding-top: 9px;
		flex-grow: 1;

		@include max(lg) {
			display: none;
		}
	}

	&__menu {
		display: flex;
		flex-wrap: wrap;

		li {
			margin-bottom: 9px;

			&:nth-child(3n+1) {
				width: 26%;
			}

			&:nth-child(3n + 2) {
				width: 31%;
			}

			&:nth-child(3n + 3) {
				width: 43%;
			}

			a {
				font-size: 16px;
				line-height: 16px;
				color: rgba(255, 255, 255, 0.5);
				border-bottom: 1px solid transparent;
				text-decoration: none;
				transition: color 0.3s ease, border-bottom-color 0.3s ease;

				@include hover {
					color: $blue-light;
					border-bottom-color: #12ff72;
				}
			}
		}
	}

	&--anim {
		position: absolute;
		bottom: 0;
		left: 0;
	}
}