.sform {
  form {
  }

  &__section {
    & + & {
      margin-top: 60px;

      @include max(md) {
        margin-top: 40px;
      }
    }
  }

  &__select {
    margin-top: 40px;
    margin-bottom: 30px;
  }

  &__field {
    max-width: 390px;

    @include max(sm) {
      max-width: none;
    }

    & + & {
      margin-top: 30px;
    }
  }

  &__file {
    margin-top: 40px;
  }

  &__bottom {
    display: flex;
    margin-top: 30px;

    @include max(sm) {
      flex-direction: column;
    }
  }

  &__send {
    align-self: flex-start;

    @include max(sm) {
      width: auto;
      align-self: flex-start;
    }
  }

  &__consent {
    margin-left: 30px;
    font-size: 16px;
    line-height: 1.3;
    font-weight: 300;
    color: rgba($darkColor2, 0.5);

    @include max(sm) {
      width: 100%;
      margin-bottom: 15px;
      margin-left: 0;
      align-self: flex-start;
      order: -1;
    }

    a {
      color: rgba($darkColor2, 0.5) !important;
      text-decoration: underline !important;

      @include hover {
        text-decoration: none !important;
      }

      &:active {
        opacity: 0.7;
      }
    }
  }
}
